import { Component, EventEmitter, Element, Prop, State, Event, h, Listen, Method } from '@stencil/core';

@Component({
  tag: 'hdc-multiselect',
  styleUrl: 'hdc-multiselect.scss',
})
export class HdcMultiselect {
  /**
   * Element of hdc multiselect
   */
  @Element() el: HTMLElement;
  /**
   * Prop value of hdc multiselect
   */
  @Prop({ mutable: true }) value: Array<string | object> | null;
  /**
   * Prop disable of hdc multiselect
   */
  @Prop({ mutable: true }) disable: boolean;
  /**
   * Prop lock of hdc multiselect
   */
  @Prop({ mutable: true }) lock: boolean;
  /**
   * Prop items of hdc multiselect
   */
  @Prop({ mutable: true }) items: Array<string | object> | undefined;
  /**
   * Prop view items of hdc multiselect
   */
  @Prop({ mutable: true }) viewItems: number = 8;
  /**
   * Prop key of hdc multiselect
   */
  @Prop({ mutable: true }) valueKey = 'name';
  /**
   * Prop string of hdc multiselect
   */
  @Prop() valueLabel = 'name';
  /**
   * State filtered items of hdc multiselect
   */
  @State() filteredItems: Array<string | object> = [];
  /**
   * State search text of hdc multiselect
   */
  @State() searchText = '';
  /**
   * State search text of hdc multiselect
   */
  @State() valueOptions: Array<string | object> | null;
  /**
   * State search text of hdc multiselect
   */
  @State() showDrop: boolean = false;
  /**
   * State search text of hdc multiselect
   */
  @State() isSelectAll: boolean = false;
  /**
   * Event change value of hdc multiselect
   */
  @Event() changeValue: EventEmitter<string | object | null>;
  /**
     * Max height of hdc multiselect
     */
  maxHeight = 20;

  /**
   * Determines whether change value filter options on
   * @param event CustomEvent
   */
  componentWillUpdate(){
    if(!this.value) {
      this.valueOptions = null;
      this.value = null;
    }
  }
  onChangeValueFilterOptions(event: string) {
    this.searchText = event;
    this.filteredItems =
      this.items?.filter((option) => {
        const label =
          this.valueLabel && typeof option === 'object' ? option[this.valueLabel] : option;

        return label.toLowerCase().includes(this.searchText.toLowerCase());
      }) ?? [];
      requestAnimationFrame(() => {
        const list = this.el.querySelectorAll('input[data-name="selectItems"]');
        if (list) {
          list.forEach(item => {
            const checkbox = item as HTMLInputElement;
            let flag = this.valueOptions?.map(item => String(item[this.valueKey])).includes(String(checkbox.value));
            if (flag) {
              checkbox.checked = true;
            } else {
              checkbox.checked = false;
            }
          });
        }        // Do something with liElements
      });
  }

  /**
   * Determines whether click select option on
   * @param option string
   */
  onClickSelectOption(option: string | object) {
    //Verificamos si ya existen valores seleccionados
    if (this.valueOptions) {
      const indexOf = this.valueOptions.findIndex(item => String(item[this.valueKey]) === String(option[this.valueKey]));
      if (indexOf === -1) {
        this.valueOptions.push(option);
      } else {
        this.valueOptions.splice(indexOf, 1);
      }
      if (this.valueOptions.length === 0) {
        this.valueOptions = null;
      }
    } else {
      this.valueOptions = [option];
    }

    //Desactivamos el check selectAll 
    const list = this.el.querySelectorAll('input[data-name="selectAll"]');
    if(list){
      this.isSelectAll = this.valueOptions?.length === this.items?.length;
      list.forEach((item: HTMLInputElement) => {
        item.checked = this.isSelectAll;
      });
    }
    
    this.value = this.valueOptions?.map(item => item[this.valueKey]) || null;
    this.changeValue.emit(this.value);
  }

  @Listen('click', { target: 'window' })
  handleOnClick(ev) {
    if (this.showDrop) {
      if (this.el.contains(ev.target)) {
        // If click was inside, stop here
        return;
      }
      this.showDrop = false;
    }
  }

  render() {
    return (
      <div class="hdc-multiselect">
        <button
          class="hdc-multiselect-value"
          disabled={this.disable || this.lock}
          onClick={() => this.toggleDrop()}>
          <span>{this.getValueText()}</span>
        </button>
        <div
          class={this.getClassInputAutocompleteContainer()}
        >
          <div class="hdc-multiselect-search">
            <input
              class="hdc-multiselect-search-input"
              onInput={ev => this.onInputText(ev)}
            />
          </div>
          <ul class={this.getClassInputAutocomplete()} style={{ maxHeight: this.getMaxHeight() }}>
            <li class="hdc-multiselect-item">
              <label class="hdc-multiselect-item-label">
                <input 
                  type="checkbox" 
                  data-name="selectAll" 
                  onClick={() => this.selectAllOptions()} 
                />
                <span>{'[Seleccionar Todo]'}</span>
              </label>
            </li>
            {this.filteredItems.map((option, index) => (
              <li
                key={index}
                class="hdc-multiselect-item"
              >
                <label class="hdc-multiselect-item-label">
                  <input 
                    type="checkbox" 
                    data-name="selectItems" 
                    value={option[this.valueKey]} 
                    onClick={() => this.onClickSelectOption(option)} 
                  />
                  <span>{typeof option === 'object' ? option[this.valueLabel] : option}</span>
                </label>
              </li>
            ))}
          </ul>
        </div>
      </div>
    );
  }

  toggleDrop() {
    if (!this.showDrop) {
      this.onChangeValueFilterOptions('');
    }
    this.showDrop = !this.showDrop;
  }

  selectAllOptions() {
    const list = this.el.querySelectorAll('input[data-name="selectItems"]');
    if (list) {
      list.forEach(item => {
        const checkbox = item as HTMLInputElement;
        if (!this.isSelectAll) {
          checkbox.checked = true;
        } else {
          checkbox.checked = false;
        }
      });
      if (!this.isSelectAll) {
        this.valueOptions = this.items ? [...this.items] : null;
      } else {
        this.valueOptions = null;
      }
      this.value = this.valueOptions?.map(item => item[this.valueKey]) || null;
      this.changeValue.emit(this.value);
    }
    this.isSelectAll = !this.isSelectAll;
  }

  /**
   * Determines whether input text on
   * @param input Event
   */
  @Method()
  async onInputText(input: InputEvent) {
    const element = input.target as HTMLInputElement;

    this.onChangeValueFilterOptions(element.value);

   
  }

  /**
   * Gets value text
   * @returns string
   */
  getValueText() {
    if(this.value) {
      if(this.isSelectAll) {
        return 'Todos';
      } else if(this.value.length > 4) {
        return `${this.valueOptions?.length} de ${this.items?.length} seleccionados`;
      }

      return this.valueOptions?.map(item => item[this.valueLabel]).join(', ') || '';
    }
    /*
        this.valueOptions = null;
    this.value = null;
    
    */

    return '-- Seleccione --';
  }

  /**
   * Gets max height
   * @returns { [key: string]: boolean }
   */
  getMaxHeight() {
    return this.viewItems > 0 ? `${(this.maxHeight * this.viewItems) + 40}px` : '';
  }

  /**
   * Gets class multiselect container
   * @returns { [key: string]: boolean }
   */
  getClassInputAutocompleteContainer() {
    return {
      'hdc-multiselect-container': true,
      'hdc-multiselect-container--top': true,
      'show': this.showDrop,
    };
  }

  /**
   * Gets class multiselect
   * @returns { [key: string]: boolean }
   */
  getClassInputAutocomplete() {
    return {
      'hdc-multiselect-options': true,
    };
  }

}
